<template>
    <div>
        <NavBar />
        <div class="container mt-4 ml-0">
            <div class="row justify-content-start">
                <div class="col col-auto">
                    <input class="form-control searchbar" type="text" placeholder="" v-model="searchText">
                </div>
                <div class="col col-auto ml-0">
                    <button class="btn btn-primary" @click.prevent="openAddModal()"></button>
                </div>
            </div>
        </div>
        <table class="table mt-4 table-striped">
            <thead>
                <th style="width: 3%" scope="col"></th>
                <th style="width: 5%" scope="col"></th>
                <th style="width: 10%" scope="col"></th>
                <th style="width: 7%; min-width: 105px" scope="col"></th>
                <th style="width: 7%; min-width: 105px" scope="col"></th>
                <th style="width: 13%" scope="col"></th>
                <th style="width: 7%" scope="col"></th>
                <th style="width: 3%" scope="col"></th>
            </thead>
            <tbody>
                <tr v-for="(item, id) in handleIssues" :key="id">
                    <td class="align-middle">
                        <button type="button" class="btn btn-outline-secondary btn-sm"
                            @click.prevent="checkIssue(item)">
                            <font-awesome-icon icon="search" />
                        </button>
                    </td>
                    <td class="align-middle">
                        {{ item.number }}
                    </td>
                    <td class="align-middle">
                        {{ item.summary }}
                    </td>
                    <td class="align-middle">
                        <b-form-select v-model="item.assigneeId" :options="users" disabled></b-form-select>
                    </td>
                    <td class="align-middle">
                        <b-form-select v-model="item.reporterId" :options="users" disabled></b-form-select>
                    </td>
                    <td class="align-middle">
                        {{ item.statusId }}
                    </td>
                    <td class="align-middle">
                        <b-form-select v-model="item.createUser" :options="users" disabled></b-form-select>
                    </td>
                    <td class="align-middle">
                        <button type="button" class="btn btn-outline-danger btn-sm" @click.prevent="tempIssue = item; openDeleteModal(item)">
                            <font-awesome-icon icon="trash-alt" />
                        </button>
                    </td>
                </tr>
            </tbody>
        </table>

        <!-- Add Issue Modal -->
        <b-modal id="addIssueModal" title="" @ok="createIssue">
            <div class="modal-body">
                <div class="row">
                    <div class="col-sm">
                        <div class="form-group">
                            <label for="title"></label>
                            <input type="text" class="form-control" id="title" placeholder="" required
                                v-model="tempIssue.summary">
                        </div>
                        <div class="form-row">
                            <div class="form-group col-md-6">
                                <label for="assigneeId"></label>
                                <b-form-select v-model="tempIssue.assigneeId" id="assigneeId" :options="users" size="sm" class="mt-3" required></b-form-select>
                            </div>
                            <div class="form-group col-md-6">
                                <label for="reporterId"></label>
                                <b-form-select v-model="tempIssue.reporterId" id="reporterId" :options="users" size="sm" class="mt-3" required></b-form-select>
                            </div>
                        </div>
                        <div class="form-group">
                            <label for="description"></label>
                            <textarea type="text" class="form-control" id="description" placeholder=""
                                v-model="tempIssue.description"></textarea>
                        </div>
                        <div class="form-row">
                            <div class="form-group col-md-6">
                                <label for="statusId"></label>
                                <b-form-select v-model="tempIssue.statusId" id="statusId" :options="statusOptions" size="sm" class="mt-3" required></b-form-select>
                            </div>
                            <div class="form-group col-md-6">
                                <label for="kindId"></label>
                                <b-form-select v-model="tempIssue.kindId" id="kindId" :options="kindOptions" size="sm" class="mt-3" required></b-form-select>
                            </div>
                        </div>
                        <div class="form-row">
                            <div class="form-group col-md-6">
                                <label for="serverityId"></label>
                                <b-form-select v-model="tempIssue.serverityId" id="serverityId" :options="serverityOptions" size="sm" class="mt-3" required></b-form-select>
                            </div>
                            <div class="form-group col-md-6">
                                <label for="urgencyId"></label>
                                <b-form-select v-model="tempIssue.urgencyId" id="urgencyId" :options="urgencyOptions" size="sm" class="mt-3" required></b-form-select>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </b-modal>
    </div>
</template>

<script>
    import _ from 'lodash';
    import NavBar from '@/components/NavBar.vue';

    export default {
        name: 'IssueIndex',
        components: {
            NavBar
        },
        data() {
            return {
                searchText: '',
                createModal: false,
                tempIssue: {
                    number: '',
                    summary: '',
                    assigneeId: '',
                    reporterId: '',
                    statusId: '',
                    description: '',
                    kindId: '',
                    serverityId: '',
                    urgencyId: '',
                    projectId: ''
                },
                issues: [],
                statusOptions:[
                    { value: 1, text: '' },
                    { value: 2, text: '' },
                    { value: 3, text: '' },
                    { value: 4, text: '' },
                    { value: 5, text: '' },
                    { value: 6, text: '' }
                ],
                kindOptions:[
                    { value: 1, text: '' },
                    { value: 2, text: '' },
                    { value: 3, text: '' },
                    { value: 4, text: '' }
                ],
                serverityOptions:[
                    { value: 1, text: '' },
                    { value: 2, text: '' },
                    { value: 3, text: '' },
                    { value: 4, text: '' },
                    { value: 5, text: '' }
                ],
                urgencyOptions:[
                    { value: 1, text: '' },
                    { value: 2, text: '' },
                    { value: 3, text: '' },
                    { value: 4, text: '' }
                ],
                users:[],
                statusPresent:{ 1: '', 2: '', 3: '', 4: '', 5: '', 6: ''},
            };
        },
        computed: {
            handleIssues() {
                const vm = this;
                return _.filter(vm.issues, function (issue) {
                    return issue.summary.match(vm.searchText);
                });
            }
        },
        methods: {
            getIssueList() {
                const api = 'http://lspssapple.asuscomm.com:81/api/issue';
                const vm = this;
                const token = localStorage.getItem('token');
                const id = localStorage.getItem('user_id');
                this.$http.get(
                    api,
                    { headers: { "Authorization": "Bearer " + token, "content-type": "application/json;charset=utf-8"}}
                ).
                then((response) => {
                    console.log(response)
                    if(response.status == 200){
                        response.data.forEach(function(v, i, arr){
                            if(v.reporterId == id || v.createUser == id || v.assigneeId == id){
                                vm.issues.push(v);
                                vm.issues[vm.issues.length - 1].statusId = vm.statusPresent[vm.issues[vm.issues.length - 1].statusId]
                            }
                        })
                    }
                }); 
            },
            openAddModal() {
                this.$bvModal.show('addIssueModal');
            },
            closeAddModal() {
                this.$bvModal.hide('addIssueModal');
            },
            openDeleteModal(issue) {
                this.$bvModal.msgBoxConfirm('?',{
                    okTitle: '',
                    cancelTitle: '',
                    centered: true
                })
                .then(value => {
                    if (value){
                        const api = 'http://lspssapple.asuscomm.com:81/api/issue/' + issue.id;
                        const vm = this;
                        const token = localStorage.getItem('token');
                        this.$http.delete(
                            api,
                            { headers: { "Authorization": "Bearer " + token, "content-type": "application/json;charset=utf-8"}}
                        ).
                        then((response) => {
                            console.log(response)
                            if(response.status == 200){
                                alert('!');
                                this.$router.go();
                            }
                        }); 
                    }
                })
            },
            clearTempIssue() {
                const emptyIssue = {
                    number: '',
                    summary: '',
                    assigneeId: '',
                    reporterId: '',
                    statusId: '',
                    description: '',
                    kindId: '',
                    serverityId: '',
                    urgencyId: '',
                    projectId: ''
                };
                this.tempIssue = emptyIssue;
            },
            createIssue(){
                const api = 'http://lspssapple.asuscomm.com:81/api/issue';
                const vm = this;
                const token = localStorage.getItem('token');
                vm.tempIssue.createUser = parseInt(localStorage.getItem('user_id'));
                vm.tempIssue.number = Math.floor(100000 + Math.random() * 900000)
                this.$http.post(
                    api,
                    vm.tempIssue,
                    { headers: { "Authorization": "Bearer " + token, "content-type": "application/json;charset=utf-8"}}
                ).
                then((response) => {
                    console.log(response)
                    if(response.status == 200){
                       alert('!');
                       this.$router.go();
                    }
                }); 
            },
            checkIssue(issue){
                const vm = this;
                vm.$router.push({ path: `/issue/${issue.id}`})
            },
            getAllUser(){
                const api = 'http://lspssapple.asuscomm.com:81/api/user';
                const vm = this;
                const token = localStorage.getItem('token');
                this.$http.get(
                    api,
                    { headers: { "Authorization": "Bearer " + token, "content-type": "application/json;charset=utf-8"}}
                ).
                then((response) => {
                    // console.log(response)
                    if(response.status == 200){
                        response.data.forEach(user => vm.users.push({ value: user.id, text: user.name }));
                    }
                }); 
            },
            handleUser(id) {
                const vm = this;
                this.users.forEach(function(v, i, arr){
                    if(v.value == id){
                        console.log(v.text)
                        return v.text
                    }
                })
            }
        },
        created(){
            this.getIssueList();
            this.getAllUser();
        }
    }
</script>